html {
	body, #root {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		.App {

			.calculator {
				display: flex;
				align-items: flex-end;
				&__container {
					width: calc(67px * 4);
					display: flex;
					flex-wrap: wrap;
					&_second {
						display: flex;
						align-items: flex-start;
						@media(max-width: calc(67px * 6)) {
							overflow: hidden;
							.scroll-button {
								display: flex; } }
						.scroll-button {
							width: 100%;
							margin: 1px;
							display: none; } } }

				.result {
					width: 100%; }
				button {
					width: 64px;
					height: 64px;
					margin: 1px;
					min-width: 0; }
				@media(max-width: calc(67px * 8)) {
					width: 100vw;
					button {
						width: calc(100vw / 8 - 2px);
						height: calc(100vw / 8 - 2px); } }
				.MemoryButtons {} } } } }


